<template>
	<view style="width: 100%;height: 100%;">
		<view class="container" v-show="showPageOne">
			<view class="mymap" id="mymap">			
			</view>
			<view class="rightToll">
				<view class="tollone">
					<view class="uni-list" v-show="!layersFrame">
						<radio-group @change="radioChange">
							<label class="uni-list-cell" v-for="(item, index) in radioitems" :key="item.value">
								<view>
									<radio :value="item.value" :checked="index === current" />
								</view>
								<view>{{item.name}}</view>
							</label>
						</radio-group>
					</view>
					<view class="layersControl tollIcon" @click="layerframeControl"></view>
				</view>
				<view class="tolltwo">
					<view class="uni-list" v-show="!datasFrame">
						<checkbox-group @change="checkboxChange">
							<label class="uni-list-cell" v-for="item in checkboxitems" :key="item.value">
								<view>
									<checkbox :value="item.value" :checked="item.checked" />
								</view>
								<view>{{item.name}}</view>
							</label>
						</checkbox-group>
					</view>
					<view class="datassControl tollIcon" @click="datasframeControl"></view>
				</view>
				<view class="tollthree">
					<view class="localControl tollIcon" @click="localtionControl"></view>
				</view>
				<view class="tollfour">
					<view class="helpControl tollIcon" @click="helpControl"></view>
				</view>
			</view>
			<view class="bottomBar">
				<view v-for="(item,index) in tasks" :key="item.name" class="taskItem" v-show="!doTaskShow">
					<view class="taskItemChild" @click="navaibtn">
						<view class="itemName">{{item.name}}</view>
						<view class="naviTo">导航过去</view>
					</view>
					<view class="taskItemChild">
						<view class="itemMoney">{{item.money}}元</view>
						<view>
							<button class="getTask" size="mini" @click="toDoTask">领取任务</button>
							<!-- <button type="primary" size="mini" class="doTask">做任务</button> -->
						</view>
					</view>
				</view>
				<view class="doTaskDiv" v-show="doTaskShow">
					<view class="getTaskTitle">已领取</view>
					<view  class="taskItem haveGotTask">
						<view class="taskItemChild" @click="navaibtn">
							<view class="itemName">{{dotaskName}}</view>
							<view class="naviTo">导航过去</view>
						</view>
						<view class="taskItemChild">
							<view class="itemMoney">{{dotaskMoney}}元</view>
							<view style="display: flex;justify-content: space-between">
								<span class="doTaskTime" size="mini" >剩余{{remainTime}}</span>
								<button type="primary" size="mini" class="doTask" @click="doTask" :disabled="dotaskDis">做任务</button>
							</view>
						</view>
					</view>
				</view>
			</view>
		</view>
		<!-- neirong -->
		<view class="bhfxIndex" v-show="!showPageOne">
			<view class="phototitle">拍照</view>
			<view class="cameraDiv">
				<view class="cameraChild" @click="cameraChild(index)" v-for="(item,index) in formdata.imgfiles">
					<image :src="item.imagesrc" :key="item.id" mode="scaleToFill" ref="cameraImg" class="cameraImg"/>
				</view>
			</view>
			<view class="formDiv">
				<b>上传时间：</b>{{formdata.SUNTIME}}
			</view>
			<view class="formDiv">
				<b>拍照地点：</b>{{formdata.bhlocation}}
			</view>
			<view class="formDiv formDiv2">
				<b>是否变化：</b>
				<radio-group @change="bhyesOrno">
					<label v-for="(item,index) in radios2" class="radioLabel">
						<radio :value="item.radioval" :key="item.id" /><text>{{item.radioval}}</text>
					</label>
				</radio-group>
			</view>
			<view class="formDiv formDiv2" style="border-bottom: none;">
				<view class="title"><b>变化类型：</b></view>
				<radio-group @change="bhtypeVal">
					<label v-for="(item,index) in radios" class="radioLabel">
						<radio :value="item.radioval" :key="item.id" /><text>{{item.radioval}}</text>
					</label>
				</radio-group>
			</view>
			<view class="formDiv" style="padding-left: 80px;">
				前期：
				<select  @change="selectOne" id="selectOne">
				  <option value =""></option>
				  <option value ="建筑">建筑</option>
				  <option value ="道路">道路</option>
				  <option value="绿地">绿地</option>
				  <option value="裸地">裸地</option>
				  <option value="绿网">绿网</option>
				</select>
				后期：
				<select  @change="selectTwo" id="selectTwo">
				  <option value =""></option>
				  <option value ="建筑">建筑</option>
				  <option value ="道路">道路</option>
				  <option value="绿地">绿地</option>
				  <option value="裸地">裸地</option>
				  <option value="绿网">绿网</option>
				</select>
			</view>
			<view class="formDiv">
				<view><b>变化详情：（大于15字）</b></view>
				<textarea  placeholder="请输入您的详细描述" class="formTextarea" @blur="getTextcontent"/>
			</view>
			<view>
				<button type="primary" @click="submitImgs">提交</button>
			</view>
		</view>
		
	</view>
	
</template>

<script>
	var _this;
	import esriLoader from 'esri-loader';
	import {appLoginWx,request} from '@/pages/network/appLoginWx.js'
		export default {
			data() {
				return {
					map:null,
					showLayer:true,
					layersFrame:true,
					datasFrame:true,
					radioitems: [
						{
							value: 'imgMap',
							name: '卫星地图',
							checked: 'true'
						},
						{
							value: 'skyMap',
							name: '天地图'							
						},
					],
					checkboxitems: [
						{
							value: 'poi',
							name: 'POI'
						},
						{
							value: 'house',
							name: '房屋'							
						},
						{
							value: 'road',
							name: '道路'							
						},
					],
					current: 0,
					longitudeData:0,
					latitudeData:0,
					resconfig:null,
					view:null,
					symbol:null,
					renderer:null,
					tasks:[
						{name:"羊坊店路",money:"10"},
						{name:"西站",money:"20"},
						{name:"军博",money:"10"}
					],
					doTaskShow:false,
					dotaskMoney:'10',
					dotaskName:'羊坊店',
					remainTime:'30:00',
					dotaskDis:false,
					showPageOne:true,
					formdata:{
						imgfiles:[
							{
								imagesrc:"data:image/jpeg;base64,",
								id:0,
							},
							{
								imagesrc:"data:image/jpeg;base64,",
								id:1,
							},
							{
								imagesrc:"data:image/jpeg;base64,",
								id:2,
							}
						],
						SUNTIME:"2020",						
						BHTYPE:"拆除",
						BHREMARK:"",						
						BHBEFORE:'绿地',
						BHAFTER:'裸地',
						bhlocation:'经纬度',	
						graphicId:2020,
						oldGraphicId:2019,
						newType:'纠错',
						bhyesIsno:"是"
					},
					radios:[
						{
							id:0,
							radioval:"新增"
						},
						{
							id:1,
							radioval:"拆除"
						},
						{
							id:2,
							radioval:"改造"
						},
					],
					radios2:[						
						{
							id:1,
							radioval:"是"
						},
						{
							id:0,
							radioval:"否"
						}
					],
					imgfilesNew:[
						{
							imagesrc:"https://www.sunset.com/wp-content/uploads/96006df453533f4c982212b8cc7882f5-800x0-c-default.jpg",
							id:0,
						},
						{
							imagesrc:"https://www.sunset.com/wp-content/uploads/96006df453533f4c982212b8cc7882f5-800x0-c-default.jpg",
							id:1,
						},
						{
							imagesrc:"https://www.sunset.com/wp-content/uploads/96006df453533f4c982212b8cc7882f5-800x0-c-default.jpg",
							id:2,
						},
					],
				}
			},
			mounted() {
			},
			beforeDestroy(){
				if(this.view){
					this.view.container = null;
				}
			},
			onLoad(options) {
				// console.log('web',options,this);
				// this.longitudeData=Number(options.longitude);
				// this.latitudeData=Number(options.latitude);
			},
			onShow(){
				_this=this;
				// var url1= window.location.href.split('#')[0];//获取网页ip
				// request({
				// 	url:'/api/wxuser/getconfig',
				// 	method:"POST",
				// 	data: {
				// 		url:url1
				// 	},
				// })
				// .then((res)=>{
				// 	_this.resconfig = res.data.data;
				// 	_this.$jweixin.config({
				// 	  // debug: true, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc会打印。
				// 	  appId: 'wx4d844e46e277faa1', // 必填，公众号的唯一标识
				// 	  timestamp:_this.resconfig.timestamp , // 必填，生成签名的时间戳
				// 	  nonceStr: _this.resconfig.nonceStr, // 必填，生成签名的随机串
				// 	  signature: _this.resconfig.signature,// 必填，签名
				// 	  jsApiList: ["getLocation"] ,// 必填，需要使用的JS接口列表
				// 	});
				// 	//this.$jweixin.miniProgram.getEnv(function(res){})
					_this.$jweixin.getLocation({
						type: 'wgs84', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
						  success: function (res) {
						    var latitude = res.latitude; // 纬度，浮点数，范围为90 ~ -90
						    var longitude = res.longitude; // 经度，浮点数，范围为180 ~ -180。
						    // var speed = res.speed; // 速度，以米/每秒计
						    // var accuracy = res.accuracy; // 位置精度
							_this.longitudeData=Number(longitude);
							_this.latitudeData=Number(latitude );
							_this.creatMapview();//创建地图
						  },
						  fail:function(err){
						  	console.log(JSON.stringify(err))
						  }
					})
				// },(err)=>{
				// 	console.log(JSON.stringify(err))
				// })
			},
			methods: {
				creatMapview(){
					esriLoader.loadModules(['esri/Map','esri/views/MapView','esri/layers/FeatureLayer',"esri/layers/TileLayer","esri/layers/WebTileLayer"],{
						css:true
					})
					.then(([Map,MapView,FeatureLayer,TileLayer,WebTileLayer])=>{
						let map = new Map({
							// basemap:'streets',
						});
						this.map=map;
						this.view = new MapView({
							container:'mymap',
							map:map,
							center:[this.longitudeData,this.latitudeData],
							zoom:8,
							// zoom:5,
						});
						this.view.ui._removeComponents(['zoom']);//去掉缩放图标
						this.view.ui._removeComponents(["attribution"]);//去掉ersilogo
						let imageLayer=new TileLayer({
							//影像服务"esri/layers/ImageryLayer"
							// url: "https://landsat2.arcgis.com/arcgis/rest/services/Landsat8_Views/ImageServer"							
							//MapServer"结尾:"esri/layers/MapImageLayer"动态服务，无切片信息；
							//MapServer"结尾:esri/layers/TileLayer"或者"esri/layers/WebTileLayer"切片服务有切片信息 	
							url:"https://elevation3d.arcgis.com/arcgis/rest/services/NatGeo_World_Map/MapServer"
							// url: "http://192.168.1.163:6080/arcgis/rest/services/CYImage/MapServer"
						  });
						map.add(imageLayer);//影像切片图
						let webTile=new WebTileLayer({
						   urlTemplate: 'https://stamen-tiles-{subDomain}.a.ssl.fastly.net/terrain/{level}/{col}/{row}.png',
						   subDomains: ['a', 'b', 'c', 'd'],
						   visible:false
						 });
						map.add(webTile);//天地图
						// featruesLayer
						const layerfeaturePoi = new FeatureLayer({
						  url: 'https://sampleserver6.arcgisonline.com/arcgis/rest/services/USA/MapServer/0',
						  visible:false
						});
						const layerfeatureHouse = new FeatureLayer({
						  url: 'https://sampleserver6.arcgisonline.com/arcgis/rest/services/USA/MapServer/1',
						  visible:false
						});
						const layerfeatureRoad = new FeatureLayer({
						  url: 'https://sampleserver6.arcgisonline.com/arcgis/rest/services/USA/MapServer/2',
						  visible:false
						});
						map.addMany([layerfeaturePoi,layerfeatureHouse,layerfeatureRoad]);
						// view.map.addMany([imageLayer,trailheadsLayer])
						// 坐标标记
						this.symbol = {
						   type: "picture-marker",  // autocasts as new PictureMarkerSymbol()
						   url: "data:image/svg+xml;base64,",
						   width: "20px",
						   height: "20px"
						 };
						const graphics = [{
							geometry: {
								type: 'point',
								longitude: _this.longitudeData, // 经度116.29845,39.95933
								latitude: _this.latitudeData, // 纬度
							}
						}];
						  // 设置渲染
						_this.renderer = {
							type: 'simple',
							symbol:_this.symbol,
						 };
						  // 创建要素图层
						const marklayer = new FeatureLayer({
							source: graphics,
							objectIdField: 'ObjectID',
							renderer:this.renderer,
						});
						map.add(marklayer);//添加标注						
					})
				},
				radioChange(evt) {
					if(evt.target.value=="imgMap"){
						this.map.layers.items[0].visible=this.showLayer
						this.map.layers.items[1].visible=!this.showLayer
					}else{
						this.map.layers.items[0].visible=!this.showLayer
						this.map.layers.items[1].visible=this.showLayer
					}
				},
				layerframeControl(){
					this.layersFrame=!this.layersFrame;
				},
				checkboxChange(e) {
					let items = this.checkboxitems;
					let	values = e.detail.value;debugger
					for (let i = 0, lenI = items.length; i < lenI; ++i) {
						const item = items[i]
						if(values.includes(item.value)){
							this.$set(item,'checked',true);
							this.map.layers.items[2+i].visible = true;
						}else{
							this.$set(item,'checked',false);
							this.map.layers.items[2+i].visible = false;
						}
					}
				},
				datasframeControl(){
					this.datasFrame=!this.datasFrame
				},
				localtionControl(){					
					// this.$jweixin.miniProgram.getEnv(function(res){
						_this.$jweixin.getLocation({
							type: 'wgs84', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
							  success: function (res) {
							    var latitude = res.latitude; // 纬度，浮点数，范围为90 ~ -90
							    var longitude = res.longitude; // 经度，浮点数，范围为180 ~ -180。
							    var speed = res.speed; // 速度，以米/每秒计
							    var accuracy = res.accuracy; // 位置精度								
								_this.longitudeData=-Number(longitude);
								_this.latitudeData=Number(latitude)+1;
								// _this.longitudeData=Number(longitude);
								// _this.latitudeData=Number(latitude);
								let updatelayer=_this.map.layers.items[_this.map.layers.length-1];
								_this.map.remove(updatelayer);
								const graphics = [{
									geometry: {
										type: 'point',
										longitude: _this.longitudeData, // 经度116.29845,39.95933
										latitude: _this.latitudeData, // 纬度
									}
								}];
								  // 创建要素图层
								esriLoader.loadModules(['esri/layers/FeatureLayer'])
								.then(([FeatureLayer])=>{
									const updatelayer = new FeatureLayer({
									 	source: graphics,
									 	objectIdField: 'ObjectID',
									 	renderer:_this.renderer
									});
									_this.map.add(updatelayer);//添加新标注
									_this.view.center=[_this.longitudeData,_this.latitudeData];//地图中心定位到坐标
								})
								
							  },
							  fail:function(err){
							  	console.log(JSON.stringify(err))
							  }
						})
					// })										
				},
				// helpControl(){//弹出帮助信息
				// 	uni.showModal({	
				// 		title: '纠错信息',					
				// 	    content:`这是纠错按钮`,
				// 		showCancel: false,						
				// 		confirmText: '关闭'
				// 	})
						
				// },
				toDoTask(){
					this.doTaskShow=true;
					_this.resetTime(30);
				},
				doTask(){
					this.doTaskShow=false;
					this.showPageOne=false;
				},
				resetTime(timestamp){//30min倒计时
				    var timer=null;
				    var t=timestamp;
				    var m=0;
				    var s=0;
				    m=Math.floor(t/60%60);
				    m<10&&(m='0'+m);
				    s=Math.floor(t%60);
				    let countDown=()=>{
				        s--;
				        s<10&&(s='0'+s);
				        if(s.length>=3){
				            s=59;
				            m="0"+(Number(m)-1);
				        }
				        if(m.length>=3){
				            m='00';
				            s='00';
				            clearInterval(timer);
				        }
				        _this.remainTime=`${m}:${s}`;
				        if(m==0 && s==0){
				            //location.reload();//刷线页面
							_this.dotaskDis=true;
				        }
				    }
				    timer=setInterval(countDown,1000);
				},
				getforInf(){
					_this.$jweixin.getLocation({
						type: 'wgs84', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
						  success: function (res) {
						    let latitude = res.latitude; 
						    let longitude = res.longitude;
							_this.formdata.bhlocation="经度:"+longitude+"，纬度："+latitude;
						  },
						  fail:function(err){
						  	console.log(JSON.stringify(err));
						  }
					});	
				},
				getTime(){
					  let yy = new Date().getFullYear();
					  let mm = new Date().getMonth()+1;
					  let dd = new Date().getDate();
					  let hh = new Date().getHours();
					  let mf = new Date().getMinutes()<10 ? '0'+new Date().getMinutes() : new Date().getMinutes();
					  let ss = new Date().getSeconds()<10 ? '0'+new Date().getSeconds() : new Date().getSeconds();
					  _this.formdata.SUNTIME = yy+'/'+mm+'/'+dd+' '+hh+':'+mf;//时间
				},
				cameraChild(index){//照片
					uni.chooseImage({
					    count: 1, //默认9
					    sizeType: ['original'], //可以指定是原图还是压缩图，默认二者都有
					    sourceType: ['album'], //从相册选择
					    success: function (res) {
							_this.formdata.imgfiles[index].imagesrc=res.tempFiles[0].path;
							_this.getforInf();
							// request({//获取图片路径
							// 	url:'/api/photos',
							// 	data: {
							// 		imgurl:res.tempFiles[0].path
							// 	},
							// })
							// .then((res)=>{
							// 	_this.imgfilesNew[index].imagesrc=res.tempFiles[0].path;
							// })							
					    }
					});
				},
				bhyesOrno(e){
					this.formdata.bhyesIsno=e.detail.value;
				},
				bhtypeVal(e){
					this.formdata.BHTYPE=e.detail.value;
				},
				selectOne(){
					this.formdata.BHBEFORE=document.getElementById('selectOne').value;
				},
				selectTwo(){
					this.formdata.BHAFTER=document.getElementById('selectTwo').value;
				},
				getTextcontent(e){
					let	values = e.detail.value;
					this.formdata.BHREMARK=values;
				},
				submitImgs(){
					if(this.formdata.BHREMARK.length<15){
						uni.showToast({
							icon: 'none',
							position: 'bottom',
							title: '变化详情不能少于15个字'
						});
					}else{
						// uni.request({
						//     url: 'https://www.example.com/request'
						//     data: {
						//         imgs:_this.formdata.imgfiles,
						// 		   graphicId:_this.formdata.graphicId
						//     },
						//     success: (res) => {
						//         console.log(res.data);

						//     }
						// });
					}
				},
			}
		}
</script>

<style>
	@import url("./css/taskIndex.css");
	.bhfxIndex{padding: 0 5px;}
	.phototitle{font-weight: bold;padding:0 0 5px 0;}
	.cameraDiv{height: 150px;width: 100%;display: flex;justify-content:space-around;}
	.formDiv{margin: 5px 0;border-bottom: solid 1px lightgray;padding: 5px 0;}
	.formDiv2{display: flex;justify-content: flex-start;}
	.cameraChild{width: 33.33%;}
	.cameraImg{width:100%;height:100%;border: solid 1px lightgray;}
	.radioLabel{margin: 0 5px;}
	.radioColor{color: gray;}
	.formTextarea{width: 100%;height: 150rpx;background: lightgray;}
</style>
